.MovieCard {
  position: relative;
  -ms-flex: 0 0 32%;
      flex: 0 0 32%;
  margin-right: 2%;
  padding: 8px;
  margin-bottom: 16px;
  color: #424242;
  -webkit-box-shadow: 0 0 2px 2px rgba(33, 33, 33, 0.5);
          box-shadow: 0 0 2px 2px rgba(33, 33, 33, 0.5);
  background-color: #fff;
  border-radius: 2px;
}

.MovieCard:nth-child(3n) {
  margin-right: 0;
}

.MovieCard__btn {
  position: absolute;
  outline: 0;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}

.MovieCard__btn--del {
  background-color: #ff5722;
  color: #fff;
  top: 8px;
  right: 8px;
  padding: 4px 10px;
  font-size: 16px;
  border-radius: 4px;
  text-transform: capitalize;
  -webkit-box-shadow: 0 2px 2px 0 rgba(33, 33, 33, 0.5);
          box-shadow: 0 2px 2px 0 rgba(33, 33, 33, 0.5);
  -webkit-transition: all 0.3s linear;
  -o-transition: all 0.3s linear;
  transition: all 0.3s linear;
}

.MovieCard__btn--del:hover {
  -webkit-box-shadow: 0 4px 2px 0 rgba(33, 33, 33, 0.5);
          box-shadow: 0 4px 2px 0 rgba(33, 33, 33, 0.5);
  background-color: #3f51b5;
}

.MovieCard__img {
  display: block;
  width: 40%;
  height: auto;
}



